#PresupuestosView {
    padding: 35px;
    display: grid;
    justify-items: center;
    align-items: start;
}

#presupuestos {
    display: grid;
    grid-template-columns: auto 1fr;
    grid-template-rows: auto 1fr;
    grid-template-areas:
        "listaHead mensajeHead"
        "lista mensaje";
    justify-items: stretch;
    align-items: stretch;
    max-width: 450px;
    -webkit-transition: 0.8s ease-in-out;
    transition: 0.8s ease-in-out;
}

#presupuestos #listaHead {
    grid-area: listaHead;
    position: relative;
    padding: 20px;
    background: #f5f5f5;
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}

#presupuestos #listaHead h6 {
    margin: 0;
    color: rgba(0, 0, 0, 0.54);
}

#listaHead .fab {
    position: absolute;
    right: 0;
    bottom: -50%;
    z-index: 500;
    padding: 0 25px;
}

#presupuestos #lista {
    grid-area: lista;
    min-width: 450px;
    max-width: 450px;
    padding-top: 20px;
}

#presupuestos #mensajeHead {
    grid-area: mensajeHead;
    display: flex;
    justify-content: space-between;
    align-items: center;
    min-width: 600px;
    padding: 0 10px;
    background: #f5f5f5;
    border-left: 1px solid rgba(0, 0, 0, 0.1);
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}

#presupuestos #mensajeHead > button {
    border-radius: 100%;
}

#presupuestos #mensajeHead .enviar {
    display: inline-block;
    justify-self: end;
    max-width: 335px;
    padding: 0;
    margin: 10px;
    border-radius: 5px;
    background: #fff;
    text-align: right;
    box-shadow: inset 0px 1px 3px rgba(0, 0, 0, 0.2);
    overflow: hidden;
}

#presupuestos #mensajeHead .enviar input[type="email"] {
    width: 250px;
    margin: 0;
    background: none;
    border: none;
}

#presupuestos #mensajeHead .enviar button {
    color: rgba(255, 255, 255, 1);
    background: #2196f3;
    box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.2);
}

#presupuestos #mensajeHead .enviar button:hover {
    background-color: #1976d2;
    box-shadow: 0px 3px 5px rgba(0, 0, 0, 0.2);
}

#presupuestos #mensaje {
    grid-area: mensaje;
    overflow-y: scroll;
    max-width: 670px;
    max-height: 0;
    border-left: 1px solid rgba(0, 0, 0, 0.1);
}

#presupuestos .presupuesto {
    display: flex;
    align-items: center;
    justify-content: space-between;
    cursor: pointer;
}

#presupuestos .presupuesto:hover {
    cursor: pointer;
    background: rgba(0, 0, 0, 0.05);
    box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.2);
}

#presupuestos .presupuesto li {
    display: inline-block;
    padding: 10px;
}

#presupuestos .presupuesto li:first-child {
    color: rgba(0, 0, 0, 0.54);
}

#presupuestos .presupuesto li span {
    margin: 0 10px;
    color: rgba(0, 0, 0, 0.54);
}

#presupuestos .presupuesto button {
    border-radius: 100%;
}

#patente {
    grid-area: patente;
}

#patente input {
    text-transform: uppercase;
}

#km {
    grid-area: km;
}

#reparacion {
    grid-area: reparacion;
}

#reparacion input {
    text-transform: capitalize;
}

#labor {
    grid-area: labor;
}

.overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    opacity: 0;
    visibility: hidden;
    z-index: 3;
    background: rgba(0, 0, 0, 0.5);
}

.overlay--show {
    opacity: 1;
    visibility: visible;
}

.dialogs {
    position: fixed;
    top: 0;
    left: 0;
    opacity: 0;
    width: 100%;
    height: 100%;
    padding: 20px;
    visibility: hidden;
    z-index: 5;
    display: flex;
    justify-content: center;
    align-items: center;
    pointer-events: none;
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
}

.dialogs--show {
    opacity: 1;
    visibility: visible;
    -webkit-transform: scale(1);
            transform: scale(1);
}

.dialog {
    background: #fff;
    min-width: 300px;
    max-width: 500px;
    max-height: 100%;
    padding: 10px 0;
    border-radius: 5px;
    border: 1px solid rgba(0, 0, 0, 0.1);
    overflow: hidden;
    box-shadow: 0px 10px 50px rgba(0, 0, 0, 0.5);
    pointer-events: all;
    text-align: left;
}

.dialog__head {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 40px 35px;
}

.dialog__head h5 {
    margin: 0;
}

.info {
    display: inline-block;
    grid-area: info;
    text-align: right;
}

.info i {
    margin-right: 10px;
}

.dialog .body {
    grid-area: body;
    margin: 25px 30px;
    color: rgba(0, 0, 0, 0.6);
}

.dialog__buttons {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    width: 100%;
    padding: 10px 20px 10px 20px;
}

.dialog__total {
    flex-grow: 1;
    padding: 0px 20px;
    text-align: left;
    font-size: 15px;
    font-weight: 500;
    text-transform: uppercase;
}

.dialog__total b {
    font-size: 18px;
    color: #4caf50;
    margin-left: 10px;
}

.dialog > ul {
    display: grid;
    justify-items: stretch;
    align-items: center;
    grid-gap: 10px 30px;
    gap: 10px 30px;
    padding: 0px 30px;
}

.dialog span {
    margin: 0 10px;
    font-size: 13px;
    color: rgba(0, 0, 0, 0.6);
}

.dialog .error span {
    color: #ff0000;
}

.dialog .error input {
    border-radius: 4px 4px 0 0;
    border-bottom: 2px solid red;
}

